@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}

.expense-tracker{width:100%;min-height:100vh;background-color:#202442}.expense-tracker .expense-title-container{width:100%;height:10vh;background-color:transparent;display:flex;align-items:center;justify-content:center}.expense-tracker .expense-title-container h2{font-size:24px;font-weight:400;color:#fff}@media (max-width: 768px){.expense-tracker .expense-title-container h2{font-size:20px}}@media (max-width: 380px){.expense-tracker .expense-title-container h2{font-size:18px}}@media (max-width: 290px){.expense-tracker .expense-title-container h2{font-size:16px}}.expense-tracker .expense-body-container{width:100%;height:90vh;padding:0 5rem}@media (max-width: 768px){.expense-tracker .expense-body-container{padding:0 3rem}}@media (max-width: 380px){.expense-tracker .expense-body-container{padding:0 2rem}}@media (max-width: 290px){.expense-tracker .expense-body-container{padding:0 .5rem}}.expense-tracker .expense-body-container .expense-body-inner-container{width:100%;min-height:80vh;border-radius:9px;padding:2rem;background-color:#25294A;display:grid;grid-template-columns:repeat(2, 32rem);grid-template-rows:repeat(2, 15rem);grid-gap:1rem;justify-content:center;align-content:center}

.box{background-color:#2D325A;border-radius:29px;overflow:hidden}

.add-transaction{width:100%;height:100%}.add-transaction .add-transaction-top{width:100%;height:4rem;display:flex;align-items:center;justify-content:center;position:relative}.add-transaction .add-transaction-top h4{font-size:12px;font-weight:400;color:#fff}.add-transaction .add-transaction-top .underline{position:absolute;bottom:0;width:90%;height:.1rem;background:#295ce3;background:linear-gradient(90deg, #295ce3 0%, #5b8ff9 52%);border-radius:.5rem;left:50%;transform:translateX(-50%)}.add-transaction .add-transaction-bottom{width:100%;height:100%;padding:1rem 2rem 0;display:flex;flex-direction:column}.add-transaction .add-transaction-bottom label{font-size:12px;font-weight:400;color:#fff}.add-transaction .add-transaction-bottom .input-container input{height:2rem;margin:1rem 0;outline:none;padding-left:.5rem;color:white;border-radius:3px;border:1px solid rgba(0,0,0,0.123);background-color:rgba(255,255,255,0.096);margin-right:1rem}.add-transaction .add-transaction-bottom .input-container input:nth-child(1){width:70%}.add-transaction .add-transaction-bottom .input-container input:nth-child(2){width:20%}.add-transaction .add-transaction-bottom button{width:7rem;height:2rem;outline:none;cursor:pointer;border-radius:5px;border:none;margin-right:1rem;color:rgba(255,255,255,0.945);font-weight:300;font-size:12px}.add-transaction .add-transaction-bottom button:nth-child(1){background:#295ce3;background:linear-gradient(90deg, #295ce3 0%, #5b8ff9 52%)}.add-transaction .add-transaction-bottom button:nth-child(1):hover{background:#2347aa;background:linear-gradient(90deg, #1d41a1 0%, #3e61a8 52%)}.add-transaction .add-transaction-bottom button:nth-child(2){background:#6437d9;background:linear-gradient(90deg, #6437d9 0%, #9f7bf1 67%);width:6rem}.add-transaction .add-transaction-bottom button:nth-child(2):hover{background:#472b8f;background:linear-gradient(90deg, #482899 0%, #5e4892 67%)}

.balance{height:100%;width:100%;display:flex}.balance .balance-left-section{width:50%;height:100%;display:flex;align-items:center;justify-content:center}.balance .balance-left-section .balance-left-section-container{width:90%;height:60%;border-radius:24px;overflow:hidden;display:flex;align-items:center;justify-content:center;flex-direction:column;background:#295ce3;background:linear-gradient(90deg, #295ce3 0%, #5b8ff9 52%)}.balance .balance-left-section .balance-left-section-container h4{font-size:12px;font-weight:300;color:#fff}.balance .balance-left-section .balance-left-section-container h2{font-size:24px;font-weight:500;color:#fff}.balance .balance-right-section{width:50%;height:100%}.balance .balance-right-section .balance-right-top-section{width:100%;height:50%;display:flex;align-items:center;justify-content:center;flex-direction:column}.balance .balance-right-section .balance-right-top-section .balance-right-top-section-container{width:80%;height:80%;border-radius:24px;overflow:hidden;display:flex;align-items:center;justify-content:center;flex-direction:column;background:#295ce3;background:linear-gradient(90deg, #295ce3 0%, #5b8ff9 52%)}.balance .balance-right-section .balance-right-top-section .balance-right-top-section-container h4{font-size:12px;font-weight:300;color:#fff}.balance .balance-right-section .balance-right-top-section .balance-right-top-section-container h2{font-size:24px;font-weight:500;color:#fff}.balance .balance-right-section .balance-right-bottom-section{width:100%;height:50%;display:flex;align-items:center;justify-content:center;flex-direction:column}.balance .balance-right-section .balance-right-bottom-section .balance-right-bottom-section-container{width:80%;height:80%;border-radius:24px;overflow:hidden;display:flex;align-items:center;justify-content:center;flex-direction:column;background:#6437d9;background:linear-gradient(90deg, #6437d9 0%, #9f7bf1 67%)}.balance .balance-right-section .balance-right-bottom-section .balance-right-bottom-section-container h4{font-size:12px;font-weight:300;color:#fff}.balance .balance-right-section .balance-right-bottom-section .balance-right-bottom-section-container h2{font-size:24px;font-weight:500;color:#fff}

.graph{width:100%;height:100%;padding:2rem}

.history-item{width:100%;height:1.4rem;margin-bottom:5px;overflow:hidden;border-radius:.2rem;display:flex;align-items:center;justify-content:space-between}.history-item .history-circle-section{padding-left:.6rem}.history-item .history-circle-section .circle{width:.5rem;height:.5rem;border-radius:50%;background-color:#fff}.history-item .history-title-section{display:flex;justify-content:flex-start;align-items:center;padding-left:1rem;width:100%}.history-item .history-title-section h5{font-size:12px;font-weight:400;color:#fff;margin-right:.5rem}.history-item .history-title-section h5:nth-child(2){font-size:9px;font-weight:400;color:#fff}.history-item .history-delete-section{width:2rem;height:100%;background-color:rgba(0,0,0,0.39);display:flex;align-items:center;justify-content:center;overflow:hidden}.history-item .history-delete-section .history-delete-btn{width:70%;height:4px;border-radius:3px;background-color:#ffffffb6}.positive{background:#295ce3;background:linear-gradient(90deg, #295ce3 0%, #5b8ff9 52%)}

.history{width:100%;height:100%}.history .history-top-section{width:100%;height:4rem;display:flex;align-items:center;justify-content:center;position:relative}.history .history-top-section h4{font-size:12px;font-weight:400;color:#fff}.history .history-top-section .underline{position:absolute;bottom:0;width:90%;height:.1rem;background:#295ce3;background:linear-gradient(90deg, #295ce3 0%, #5b8ff9 52%);border-radius:.5rem;left:50%;transform:translateX(-50%)}.history .history-bottom-section{width:100%;height:100%;padding:1rem 2rem 0;overflow:scroll}.history .history-bottom-section .history-list-container{width:100%;height:100%}

